import React,{ useEffect } from "react";
const { kakao } = window;

function Kakao() {
	useEffect(() => {
		var mapContainer = document.getElementById('hadan'),
    	mapOption = { 
        	center: new kakao.maps.LatLng(35.11133307638991, 128.9640575744152), 
        	level: 3 // 지도의 확대 레벨
    	};

		var map = new kakao.maps.Map(mapContainer, mapOption);

		var markerPosition  = new kakao.maps.LatLng(35.11133307638991, 128.9640575744152); 

		var marker = new kakao.maps.Marker({
    		position: markerPosition
		});

		marker.setMap(map);
	},[])
	
	
	return (
		<div id="hadan" style={{width:'500px',height:'500px'}}>
		</div>
	)
}

export default Kakao;
