import '../styles/Header.css';
import { Helmet } from 'react-helmet-async';
import { Link, useLocation } from 'react-router-dom';
import $ from 'jquery';
import React, { useEffect, useState } from 'react';
import logoImg from "../images/new_logo.png";
import styled from 'styled-components';

const Main = styled.div`
	visibility: ${(props) => (props.loaded ? 'visible' : 'hidden')};
`;

function Header() {
	const location = useLocation();
	const [isLoaded, setIsLoaded] = useState(false);

	useEffect(() => {
		setIsLoaded(true);
		$(document).ready(function () {
			$('.more-btn').click(function () {
				$(this).toggleClass('active');
				$('.main-navigation').toggleClass('active');
			})
		});
	}, [location]);

	return (
		<Main loaded={isLoaded}>
			<div>
				<Helmet>
					<meta name="title" content="홀덤포커스, 홀덤 프랜차이즈, 홀덤펍 프랜차이즈, 홀덤포커스 게임소개, 이벤트 소개, 지점 안내, 가맹 문의 창업 문의" />
					<meta name="description" content="홀덤포커스, 홀덤 프랜차이즈, 홀덤펍 프랜차이즈, 홀덤포커스 게임소개, 이벤트 소개, 지점 안내, 가맹 문의 창업 문의" />
				</Helmet>
				<div className="container">
					<h1><Link to="/"><img src={logoImg} className="logo" alt=""></img></Link></h1>
					<nav className="main-navigation">
						<Link to='/' style={{ textDecoration: "none", color: "white" }}>홀덤포커스</Link>
						<Link to='/Category2' style={{ textDecoration: "none", color: "white" }}>게임소개</Link>
						<Link to='/Category3' style={{ textDecoration: "none", color: "white" }}>이벤트 소개</Link>
						<Link to='/Category4' style={{ textDecoration: "none", color: "white" }}>지점 안내</Link>
						<Link to='/Category5' className="btn" style={{ textDecoration: "none", color: "white" }}>가맹 문의</Link>
					</nav>
					<button className="more-btn">
						<i className="icon"></i>
					</button>
				</div>
			</div>
		</Main>
	);
}

export default Header;