import { useLocation } from 'react-router-dom';
import '../styles/Footer.css'
import React, { useState, useEffect } from 'react';
import logo from "../images/black-logo.png";
import styled from 'styled-components';

const FooterContainer = styled.div`
  visibility: ${props => props.visible ? 'visible' : 'hidden'};
`;

const Footer = () => {
  const [visible, setVisible] = useState(false);
  const location = useLocation();

  useEffect(() => {
    setVisible(true);
  }, []);

  useEffect(() => {
    setVisible(false);
    setTimeout(() => {
      setVisible(true);
    }, 0);
  }, [location]);

  return (
    <FooterContainer visible={visible}>
      <div>
        <footer>
          <div className="footer_box">
            <div className="footer_logo">
              <img src={logo} className="footer_logo" alt=""></img>
            </div>
            <div className="address">
              <ul>
                <li className="txt">본사 : 부산광역시 진구 중앙대로 680번가길 74, 5층</li>
                <li className="txt">TEL : 010-8700-2129</li>
                <li className="txt">COPYRIGHT (C) Hold'em Focus</li>
              </ul>
            </div>
          </div>
        </footer>
      </div>
    </FooterContainer>
  );
};

export default Footer;
