import ReactDOM from 'react-dom'; // 수정된 부분
import { HelmetProvider } from "react-helmet-async";
import React from 'react';
import './index.css';
import App from './App';


const rootElement = document.getElementById("root");
if (rootElement.hasChildNodes()) {
  ReactDOM.hydrate(<HelmetProvider><App /></HelmetProvider>, rootElement);
} else {
  ReactDOM.render(<HelmetProvider><App /></HelmetProvider>, rootElement);
}